@import url('https://fonts.googleapis.com/css?family=Poppins');
@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
  height: 100%;
  margin: 0;
}

html {
  background: linear-gradient(
          142deg,
          #abdcff 0%,
          /* #ffffff 30%, */
          #eee 30%
  );
  background-repeat: cover;
  background-repeat: no-repeat;
  background-color: #eee;
}

body {
  font-family: sans-serif;
  max-width: 100%;
  padding: 2rem 0;
  margin: 0 auto;
  color: #141414;
  display: grid;
  /* place-items: center; */
}

main {
  max-width: 1200px;
  min-width: 900px;
  /* width: 100%; */
  margin: 0 auto; /* Centers the content */
  padding: 2rem;
}

.welcome-image {
  width: 70px;
  margin-top: 40px;
}

.background-main-dark {
  background-color: #141823;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cookie-consent-quote-image {
  margin-right: 15px;
  float: left;
}
.cookie-consent-quote-text {
  position: relative;
}
.cookie-consent-quote-text::after {
  content: "“";
  font-size: 1.2em;
  position: relative;
  top: 12px;
  color: #eee;
}

.cookie-consent-quote-text::before {
  content: "”";
  font-size: 1.4em;
  position: relative;
  left: -2px;
  color: #eee;
}


.mobile-menu-open {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
  z-index: 10;
  /* margin-top:150px; */
  background: rgba(0, 0, 0, 0.85);
}

.mobile-menu-open a::before {
  content: "› "
}

.mobile-menu-open a {
  font-size: 1.2rem; /* Increase font size for better tap targets */
  color: white;
  margin: 1rem 0;
  text-decoration: none;
}


.mobile-menu-open.show {
  opacity: 1;
  visibility: visible;
  z-index: 9;
}

.mobile-menu-close-icon {
  z-index: 20;
}


#menu-block .welcome-link {
  /* min-width: 100px; */
  margin-left: 15px;
}

/* Responsive Design for Mobile */
@media (max-width: 768px) {
  body {
    padding: 1rem; /* Reduce padding for smaller screens */
    font-size: 1.2rem;
  }

  main {
    padding: 1rem;
  }

  .welcome-image {
    width: 40px; /* Further reduce size for smaller devices */
  }

  .cookie-consent-quote-image {
    width: 50px; /* Adjust image size */
  }

  .cookie-consent-quote-text {
    font-size: 0.9rem; /* Adjust font size for readability */
  }

  .mobile-menu-open a {
    font-size: 1rem; /* Adjust font size for smaller screens */
  }
}

.snippet-title-line {
  font-style: bold;
}